'use client'

import { useUser as useAuth0User } from '@auth0/nextjs-auth0'

import { isVisionLocal } from '@common/core/utils/isVisionLocal'

/**
 * Wrapper for Auth0 useUser hook that returns empty user for Vision local mode
 * This prevents Auth0 from making requests to /api/auth/profile in local mode
 */
export function useUser() {
  const isLocal = isVisionLocal()

  // For Vision local mode, return empty user without calling Auth0 hook
  // This prevents unnecessary API calls in local development

  if (isLocal) {
    return {
      user: undefined,
      error: undefined,
      isLoading: false,
      checkSession: async () => {},
    }
  }

  // For hosted mode, use Auth0 normally
  // eslint-disable-next-line react-hooks/rules-of-hooks
  return useAuth0User()
}
